.portfolio_text{
    text-align: center;
    font-size: 50px;
    color: #FFF;
    font-family: 'Leckerli One','Roboto','Caveat','I_Ngaan';
    font-weight: 200;
    margin: 15px 0 0px;
}

@media screen and (max-width: 850px) {
    .classProject>div{
        border: #FFF 1px solid;
        color: #FFF;
        margin: 0;
        border-radius: 20px;
        margin: 10px 20px;
        padding: 2%;
        height: max-content;
        display: block;
    }
    .classPj_text{
        width: 100%;
    }
    .classPj_demo{
        width: 83%;
        height: 250px;
        text-align: center;
    }
    .linkToGithub{
        width: fit-content;
        padding-left: 10px;
        display: flex;
        align-items: flex-end;
        margin-bottom: 10px;
        margin-top: 10px;
    }
    .sliderCss{
        width:100%;
        height: 210px;
        margin-left: 10%;
        padding-right: 100px;
        text-align: center;
    }
    .sliderCss img{
        height: 170px;
    }
    .sliderCss >div{
        width: 100%;
        height: 230px;
    }
}
@media screen and (min-width: 850px) {
    .classProject>div{
        border: #FFF 1px solid;
        color: #FFF;
        margin: 0;
        border-radius: 20px;
        margin: 10px 20px;
        padding: 10px;
        display: flex;
        height: 250px;
    }
    .classProject>div>div{
        height: 100%;
        display: flex;
        flex-direction: column;
    }
    .classPj_text{
        width: 55%;
    }
    .classPj_demo{
        width: 50%;
        height: 250px;
    }
    .linkToGithub{
        width: fit-content;
        padding-left: 10px;
        flex-grow: 1;
        display: flex;
        align-items: flex-end;
        margin-bottom: 10px;
    }
    .sliderCss{
        width:80%;
        height: 230px;
        margin-left: 10%;
        padding-right: 100px;
        text-align: center;
    }
    .sliderCss img{
        height: 210px;
        margin-top: -10px;
    }
    .sliderCss >div{
        width: 100%;
        height: 250px;
    }
}

.sliderCss h3>div{
    font-weight: 200;
    background-color: rgba(0, 0, 0, 0.5);
    border-left: 1px #FFF solid;
    border-top: 1px #FFF solid;
    margin-bottom: 0;
}

.classProject>div>div>p{
    margin: 5px 0;
    flex: 0 0;
}


.linkToGithub a{
    background-color: #9fcdf8;
    border-radius: 20px;
    color: #000;
    width: 100%;
    height: max-content;
    padding: 5px 20px;
    text-decoration: none;
}
.linkToGithub a:hover{
    color: #FFF;/*rgba(66, 108, 148,1)*/
    background-color: #67aff3;
}
.classProject iframe{
    text-align: center;
    margin: 5px 5%;
}


